
.band-top {
  line-height: 35px;
  position: relative;
  height: 35px;
  display: -webkit-box;
  padding: 10px;
  background: #ffffff;
  margin-bottom: 10px;
}
.band-top .time {
  color: #fba818;
  padding: 0 10px;
  font-size: 18px;
  font-weight: bold;
  margin-left: 24px;
}
.band-top .time:before {
  content: "";
  display: block;
  position: absolute;
  left: 10px;
  top: 16px;
  vertical-align: inherit;
  width: 24px;
  height: 24px;
  background: url("../assets/timer.png") no-repeat center;
  background-size: 100% 100%;
}
.m-cen-card {
  padding: 10px 10px 0;
  background: #ffffff;
}
.m-cen-card a {
  font-size: 18px;
}
.m-cen-card a.u-al {
  color: #ff9a14;
  text-decoration: underline;
}
.m-cen-card .u-head-cp {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 10px 0 0 0;
}
.m-cen-card .u-head-cp p {
  padding: 0 0 10px 0;
  font-size: 18px;
  color: #26A2FF;
}
.m-bom {
  margin: 15px 0 0 0;
  padding: 10px;
  background: #ffffff;
}
.m-bom h2 {
  font-size: 16px;
}
.m-bom .u-box {
  width: 100%;
  height: 12.5rem;
  text-align: center;
  line-height: 12.5rem;
  font-size: 6.1rem;
  color: #42B983;
}
.m-bom .u-box .u-num1 {
  color: #f43530;
}
.btn-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px 30px;
}
.btn-wrap button {
  padding: 15px 25px;
  background: #26A2FF;
  color: #ffffff;
  border: 0;
  border-radius: 4px;
  font-size: 18px;
}



