
.backdrop {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.1);
  z-index: 999;
}
.backdrop span {
  margin: 150px 0 0 0;
}
.m-header.fixed {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
}



