html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.vux-1px,
.vux-1px-t,
.vux-1px-b,
.vux-1px-tb,
.vux-1px-l,
.vux-1px-r {
  position: relative;
}
.vux-1px:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  border: 1px solid #C7C7C7;
  color: #C7C7C7;
  height: 200%;
  -webkit-transform-origin: left top;
          transform-origin: left top;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.vux-1px-t:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-b:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-tb:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-tb:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-l:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-left: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleX(0.5);
          transform: scaleX(0.5);
}
.vux-1px-r:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 100% 0;
          transform-origin: 100% 0;
  -webkit-transform: scaleX(0.5);
          transform: scaleX(0.5);
}
.vux-tap-active {
  tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
          user-select: none;
}
.vux-tap-active:active {
  background-color: #ECECEC;
}
html {
  font-size: 14px;
}
a {
  color: #26A2FF;
}
.page-wrap {
  margin: 46px 0 46px 0;
  height: 100%;
}
.u-p10 {
  padding: 0 10px;
}
.m-card {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  background: #FFFFFF;
}
.m-card .u-head {
  height: 100px;
  width: 100px;
}
.m-card .u-head img {
  width: 100%;
  height: 100%;
}
.m-card .u-info {
  text-align: left;
  padding: 0 10px;
}
.m-card .u-info p {
  padding: 0 0 6px;
}
input:focus,
textarea:focus,
keygen:focus,
select:focus {
  border: 0;
  outline-offset: 0px;
  outline: 0;
}
.sf-icon {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: sub;
  padding: 0 8px 0 0;
  background-position: center;
}
/*图标*/
.sf-icon-card {
  background-image: url(assets/icon-card.png);
}
.sf-icon-shift {
  background-image: url(assets/icon-shift.png);
}
.sf-icon-station {
  background-image: url(assets/icon-station.png);
}
.sf-icon-date {
  background-image: url(assets/icon-date.png);
}
.sf-icon-start {
  background-image: url(assets/icon-start.png);
}
.sf-icon-end {
  background-image: url(assets/icon-end.png);
}
.weui-dialog__btn_primary {
  color: #26A2FF !important;
}
@media screen and (min-width: 321px) and (max-width: 375px) {
html {
    font-size: 14px;
}
}
@media screen and (min-width: 376px) and (max-width: 414px) {
html {
    font-size: 15px;
}
}
@media screen and (min-width: 415px) and (max-width: 639px) {
html {
    font-size: 16px;
}
}
@media screen and (min-width: 640px) and (max-width: 719px) {
html {
    font-size: 20px;
}
}
@media screen and (min-width: 720px) and (max-width: 749px) {
html {
    font-size: 24.5px;
}
}
@media screen and (min-width: 750px) and (max-width: 799px) {
html {
    font-size: 28.5px;
}
}
@media screen and (min-width: 800px) {
html {
    font-size: 32.5px;
}
}
body {
  background-color: #fbf9fe;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.page-app .weui-loadmore {
  position: absolute;
  margin: 4.5em auto;
  right: 0;
  left: 0;
}
/**
* vue-router transition
*/
.router-view {
  width: 100%;
  height: 100%;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  height: 100%;
  position: absolute;
  left: 0;
}
.vux-pop-out-enter-active {
  -webkit-animation-name: popInLeft;
          animation-name: popInLeft;
}
.vux-pop-out-leave-active {
  -webkit-animation-name: popOutRight;
          animation-name: popOutRight;
}
.vux-pop-in-enter-active {
  -webkit-perspective: 1000;
          perspective: 1000;
  -webkit-animation-name: popInRight;
          animation-name: popInRight;
}
.vux-pop-in-leave-active {
  -webkit-animation-name: popOutLeft;
          animation-name: popOutLeft;
}
@-webkit-keyframes popInLeft {
from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes popInLeft {
from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes popOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
}
@keyframes popOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
}
@-webkit-keyframes popInRight {
from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes popInRight {
from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes popOutRight {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
}
@keyframes popOutRight {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
